    .list-type3 a {
        padding: 0 60px 0 12px;
        background: #0f548c;
        color: #fff;
        text-decoration: none;
    }

    .list-type3 a:before {
        content: "";
        float: left;
        position: absolute;
        top: 0;
        left: -12px;
        width: 0;
        height: 0;
        border-color: transparent #0f548c transparent transparent;
        border-style: solid;
        border-width: 16px 12px 16px 0;
    }

    .list-type3 a:after {
        content: "";
        position: absolute;
        top: 13px;
        left: 0;
        float: left;
        width: 6px;
        height: 6px;
        -moz-border-radius: 2px;
        -webkit-border-radius: 2px;
        border-radius: 2px;
        background: #fff;
        -moz-box-shadow: -1px -1px 2px #004977;
        -webkit-box-shadow: -1px -1px 2px #004977;
        box-shadow: -1px -1px 2px #004977;
    }

    .list-type3 a:hover {
        background: #00aecf;
    }

    .list-type3 a:hover:before {
        border-color: transparent #00aecf transparent transparent;
    }

